<template>
  <v-container>
    <v-tabs>
      <v-tab key="schemata">Schemata</v-tab>
      <v-tab-item key="schemata">
        <v-btn @click="toggleFilterMode">
          {{ filterButtonText }}
        </v-btn>
        <v-treeview
          :items="items"
          :activatable="!filterMode"
          transition
          :selectable="filterMode"
          :active.sync="active"
          v-model="selected"
        ></v-treeview>
      </v-tab-item>
      <v-tab key="roles">Roles</v-tab>
      <v-tab-item key="roles">
        <v-btn @click="toggleFilterMode">
          {{ filterButtonText }}
        </v-btn>
        ROLES
      </v-tab-item>
    </v-tabs>
  </v-container>
</template>

<script>
  import getDbSchemaTree from '../gql/query/getDbSchemaTree.graphql';

  export default {
    name: 'ProjectNavigator',
    computed: {
      filterButtonText () {
        return this.filterMode ? 'Apply' : 'Filter'
      }
    },
    data: () => ({
      filterMode: false,
      active: [],
      items: [],
      schemata: [],
      selected: [],
      selectedSchemata: []
    }),
    methods: {
      computeItems () {
        const selectedSchemata = this.$store.state.schemaFilter
        const schemataToDisplay = selectedSchemata.length === 0 || this.filterMode ? this.schemata : this.schemata.filter(
            s => {
              return selectedSchemata.indexOf(s.id) > -1
            }
          )

          this.items = schemataToDisplay.map(
            schema => {
              return {
                id: `schema:${schema.schemaName}`,
                name: schema.schemaName,
                children: this.filterMode ? [] : [
                  {
                    id: `tables:${schema.schemaName}`,
                    name: 'tables',
                    children: schema.schemaTables
                  },
                  {
                    id: `functions:${schema.name}`,
                    name: 'functions',
                    children: schema.schemaFunctions
                  }
                ]
              }
            }
          )
      },
      toggleFilterMode () {
        const selectedSchemata = this.$store.state.schemaFilter
        if (this.filterMode) {
          this.$store.commit('schemaFilter', { schemaFilter: this.selected })
        }
        this.filterMode = !this.filterMode
        this.computeItems()
        this.selected = selectedSchemata
      }
    },
    watch: {
      active () {
        const active = this.active[0] || ''
        const activeType = active.split(':')[0]
        
        switch (activeType) {
          case 'table':
            this.$router.push({ name: 'table', params: { id: active }})
          break
          case 'tables':
          break
          case 'function':
            this.$router.push({ name: 'function', params: { id: active }})
          break
          default:

          break
        }
      }
    },
    apollo: {
      init: {
        query: getDbSchemaTree,
        update (result) {
          this.schemata = result.allSchemata.nodes
          this.computeItems()
        }
      }
    }
  }
</script>

